<template>
  <div>
    <!--    面包屑导航-->
    <el-breadcrumb style="padding-bottom:15px " separator="/">
      <el-breadcrumb-item :to="{ path: '/main' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>文章管理</el-breadcrumb-item>
      <el-breadcrumb-item>未审核</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <!--    搜索头部-->
      <el-row :gutter="40" style="padding-bottom: 15px">
        <el-col :span="8">
          <el-input placeholder="请输入内容" v-model="input3" class="input-with-select" @keyup.enter.native="Search"
                    clearable>
            <el-button slot="append" icon="el-icon-search" @click="Search"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <el-table
          :data="noEssayList.data"
          border
          style="width: 100%">
        <el-table-column
            prop="title"
            label="标题"
            width="200">
        </el-table-column>
        <el-table-column
            prop="concent"
            label="内容"
            width="200">
        </el-table-column>
        <el-table-column
            prop="addtime"
            label="添加时间"
            width="250">
        </el-table-column>
        <el-table-column
            prop="autor"
            label="作者"
            width="200">
        </el-table-column>
        <el-table-column
            prop="statecode"
            label="状态"
            width="200">
        </el-table-column>
        <el-table-column
            prop="id"
            label="操作"
            width="203">
          <div slot-scope="a">
            <el-button type="primary" size="mini" @click="updatenoEssay(a.row)">查看/审核</el-button>
            <el-button type="danger" size="mini" @click="deletenoEssay(a.row)">删除</el-button>
          </div>
        </el-table-column>
      </el-table>
    </el-card>
    <!--      分页控件-->
    <el-pagination
        @size-change="SizeChange"
        @current-change="CurrentChange"
        :current-page="page"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="limit"
        layout="total, sizes, prev, pager, next, jumper"
        :total="noEssayList.count">
    </el-pagination>
    <el-dialog
        title="审核"
        :visible.sync="dialogUpdatenoEssay"
        width="30%">
      <!--        内容区域-->
      <el-form :model="UpdatenoEssayList"  ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="标题" prop="title">
          <el-input v-model="UpdatenoEssayList.title" ></el-input>
        </el-form-item>
        <el-form-item label="内容" prop="concent">
          <el-input  type="textarea" :rows="2" v-model="UpdatenoEssayList.concent" ></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="autor">
          <el-input   :rows="2" v-model="UpdatenoEssayList.autor " disabled></el-input>
        </el-form-item>
        <el-form-item label="添加时间" prop="addtime">
          <el-input   :rows="2" v-model="UpdatenoEssayList.addtime " disabled></el-input>
        </el-form-item>
        <el-form-item label="状态" prop="statecode">
          <el-select v-model="UpdatenoEssayList.statecode" placeholder="请选择">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
    <el-button @click="dialogUpdatenoEssay = false">取 消</el-button>
    <el-button type="primary" @click="updatenoEssay1">保 存</el-button>
  </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "noVerifyEssay",
  data(){
    return{
      options:[{
        value:'0',
        label:'审核未通过'
      },
        {
          value: '1',
          label: '审核通过'
        }
      ],
      value:'',
      input3:'',
      page: 1,
      limit: 10,
      statecode:'0',
      noEssayList:[],
      UpdatenoEssayList:{
        id:'',
        title:'',
        concent:'',
        statecode:'',
      },
      dialogUpdatenoEssay: false,
    }
  },
  methods:{
    //请求查看用户列表数据
    getnoEssayList() {
      this.$http.get("/EssayConroller/selectnoVerifyEssay", {
        params: {
          statecode: this.statecode
        }
      }).then((res) => {
         console.log(res)
        this.noEssayList = res;
        // if (res.data.code === 1) {
        //   // this.$message.success("请求成功")
        // } else this.$message.error("请求失败")
      })
    },
    //编辑按钮事件
    updatenoEssay(row) {
      this.dialogUpdatenoEssay = !this.dialogUpdatenoEssay
      this.UpdatenoEssayList = row;
      console.log(this.UpdatenoEssayList)
    },
    //确定编辑事件
    updatenoEssay1() {
      this.dialogUpdatenoEssay = !this.dialogUpdatenoEssay,
          this.$http.get("/EssayConroller/updateEssay", {
            params: {
              id: this.UpdatenoEssayList.id,
              title: this.UpdatenoEssayList.title,
              concent: this.UpdatenoEssayList.concent,
              statecode: this.UpdatenoEssayList.statecode,
            }
          }).then((res) => {
            console.log(res)
            if (res.data.code === 1) {
              this.$message.success("保存成功")
              this.getnoEssayList();
            } else this.$message.error("保存失败")
          })
    },
    //删除按钮事件
    deletenoEssay(row) {
      this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.get("/EssayConroller/deleteEssay", {
          params: {
            id: row.id,
          }
        }).then((res) => {
          console.log(res)
          if (res.data.code === 1) {
            this.$message.success("删除成功")
            this.getnoEssayList();
          } else this.$message.error("删除失败")
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    //每页条数发生改变时触发
    SizeChange(limit) {
      this.limit = limit;
      this.getnoEssayList();
    },
    //页码发生改变时触发
    CurrentChange(page) {
      this.page = page;
      this.getnoEssayList();
    },
  },
  //  钩子函数
  created() {
    this.getnoEssayList();
  },
}

</script>

<style scoped>

</style>